@charset "UTF-8";
:root {
    --base-font-size: 16px;
    --wrapper-width-small: 500px;
    --wrapper-width-medium: 800px;
    --wrapper-width-large: 1140px;
    --gutter-width: 1.25rem;
    --aside-width: 15.875rem;
    --article-width: 50.625rem;
    --line-length-minimum: 336px;
    --line-length-maximum: 480px;
    --line-length-responsive: calc(336px + (480 - 336) * ((100vw - 500px) / (1140 - 500)));
    --measure-small: 0.625rem;
    --measure-small-relative: 0.625em;
    --measure-small-proportional: 2vmin;
    --measure-medium: 1.25rem;
    --measure-medium-relative: 1.25em;
    --measure-medium-proportional: 4vmin;
    --measure-large: 2.25rem;
    --measure-large-relative: 2.25em;
    --measure-large-proportional: 8vmin;
    --measure-one: 1rem;
    --measure-one-relative: 1em;
    --opacity-alpha: 0.9;
    --opacity-beta: 0.7;
    --opacity-gamma: 0.1;
    --opacity-delta: 0.05;
    --color-transparent: transparent;
    --color-black: #060606;
    --color-mineshaft: #2b2b2b;
    --color-kaiser: #4f4f4f;
    --color-dove: #737373;
    --color-alto: #dddddd;
    --color-white: #f9f9f9;
    --color-raven: #5f8aa6;
    --color-bowhead: #507791;
    --color-coyote: #759ab3;
    --color-canada: #ff0000;
    --color-highlight: gold;
    --color-black--alpha: rgba(6, 6, 6, 0.9);
    --color-black--gamma: rgba(6, 6, 6, 0.1);
    --color-black--delta: rgba(6, 6, 6, 0.05);
    --color-raven--gamma: rgba(95, 138, 166, 0.1);
    --border-width-default: 0.25rem;
    --border-width-thin: 0.125rem;
    --border-width-hairline: 1px;
    --border-width-button: 0.182em;
    --border-style-default: solid;
    --border-style-alternate: dotted;
    --border-radius-default: 0.375rem;
    --border-radius-thin: 0.25rem;
    --border-radius-hairline: 0.125rem;
    --button-border-width: 0.182em;
    --font-display: swap;
    --font-weight-default: 400;
    --font-weight-bold: 600;
    --font-size-default: 1rem;
    --font-size-alpha: 4.5rem;
    --font-size-beta: 1.375rem;
    --font-size-gamma: 1.125rem;
    --font-size-large: 1.25rem;
    --font-size-large-relative: 1.25em;
    --font-size-small: 0.875rem;
    --font-size-small-relative: 0.875em;
    --font-family-default: "Proxima Nova", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    --font-family-alpha: "League Gothic", Impact, sans-serif;
    --font-family-serif: Georgia, serif;
    --font-family-monospace: "Input Mono", "Menlo for Powerline", "Menlo", Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
    --line-height-default: 1.875;
    --line-height-small: 1.25;
    --line-height-medium: 1.429;
    --line-height-code: 2;
    --letter-spacing-default: 0;
    --letter-spacing-uppercase: 1px;
    --animation-duration: 2s;
    --animation-function: linear;
    --rotation-default: 5deg;
    --transition-duration-default: 0.2s;
    --transition-duration-long: 1s;
    --transition-function: ease;
    --z-index-below: -1;
    --z-index-root: 0;
    --z-index-default: 1;
    --z-index-above: 2;
    --z-index-modal: 3;
    --z-index-dialog: 4;
    --z-index-notification: 5;
    --color-css: #016fba;
    --color-html: #e54d26;
    --color-javascript: #f1c54c;
    --color-liquid: #7ab55c;
    --color-markdown: #4a525a;
    --color-sass: #c6538c;
    --color-codepen: #212121;
    --color-github: #333333;
    --color-lastfm: #e31b23;
    --color-rss: #ff9770;
    --color-spotify: #1db954;
    --color-spotify-alt: #191414;
    --color-twitter: #4d9bd7;
    --icon-size: 40px;
    --logo-size: 8.5rem;
    --avatar-size: 6.25rem;
    --max-columns: 2;
}

@font-face {
    font-family: "League Gothic";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("League Gothic Regular"), url("https://chrisburnell.com/fonts/league-gothic-regular.woff2") format("woff2"), url("https://chrisburnell.com/fonts/league-gothic-regular.woff") format("woff"), url("https://chrisburnell.com/fonts/league-gothic-regular.ttf") format("truetype");
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}

@font-face {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Proxima Nova Regular"), url("https://chrisburnell.com/fonts/proxima-nova-regular.woff2") format("woff2"), url("https://chrisburnell.com/fonts/proxima-nova-regular.woff") format("woff"), url("https://chrisburnell.com/fonts/proxima-nova-regular.ttf") format("truetype");
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}

@font-face {
    font-family: "Proxima Nova";
    font-style: italic;
    font-weight: 400;
    font-display: swap;
    src: local("Proxima Nova Italic"), url("https://chrisburnell.com/fonts/proxima-nova-italic.woff2") format("woff2"), url("https://chrisburnell.com/fonts/proxima-nova-italic.woff") format("woff"), url("https://chrisburnell.com/fonts/proxima-nova-italic.ttf") format("truetype");
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}

@font-face {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: local("Proxima Nova Semibold"), url("https://chrisburnell.com/fonts/proxima-nova-semibold.woff2") format("woff2"), url("https://chrisburnell.com/fonts/proxima-nova-semibold.woff") format("woff"), url("https://chrisburnell.com/fonts/proxima-nova-semibold.ttf") format("truetype");
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}

html {
    min-height: 100%;
    position: relative;
    overflow-x: hidden;
    overflow-y: scroll;
    box-sizing: border-box;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    background-color: #f9f9f9;
    background-color: var(--color-white);
    color: #4f4f4f;
    color: var(--color-kaiser);
    width: 100%;
    margin: 0;
}

html {
    font-family: "Proxima Nova", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    font-family: var(--font-family-default);
    background-color: #4f4f4f;
    background-color: var(--color-kaiser);
    font-size: 1rem;
    font-size: var(--font-size-default);
    font-style: normal;
    font-weight: 400;
    font-weight: var(--font-weight-default);
    line-height: 1.875;
    line-height: var(--line-height-default);
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-feature-settings: "lnum" 1;
}

/**
 * Content Elements
 */
/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
    position: relative;
    font-weight: 400;
    font-weight: var(--font-weight-default);
}

h1 code, h1
kbd, h1
samp, h1
var, h1
.monospace,
h2 code,
h2
kbd,
h2
samp,
h2
var,
h2
.monospace,
h3 code,
h3
kbd,
h3
samp,
h3
var,
h3
.monospace,
h4 code,
h4
kbd,
h4
samp,
h4
var,
h4
.monospace,
h5 code,
h5
kbd,
h5
samp,
h5
var,
h5
.monospace,
h6 code,
h6
kbd,
h6
samp,
h6
var,
h6
.monospace {
    text-transform: none;
}

/**
 * Heading - Alpha
 */
.alpha,
h1 {
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: "League Gothic", Impact, sans-serif;
    font-family: var(--font-family-alpha);
    font-size: 4.5rem;
    font-size: var(--font-size-alpha);
    line-height: 1;
    text-shadow: 0.25rem 0.25rem 0 rgba(6, 6, 6, 0.05);
    text-shadow: var(--border-width-default) var(--border-width-default) 0 var(--color-black--delta);
}

@media screen and (max-width: 500px) {
    .alpha,
    h1 {
        font-size: 3.50195rem;
    }
}

/**
 * Heading - Beta
 */
.beta,
h2 {
    font-size: 1.375rem;
    font-size: var(--font-size-beta);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

/**
 * Heading - Gamma
 */
.gamma,
h3,
.lede {
    font-size: 1.125rem;
    font-size: var(--font-size-gamma);
    line-height: 1.875;
    line-height: var(--line-height-default);
}

h3 {
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 2.25rem;
    margin-top: var(--measure-large);
    border-bottom-width: 0.125rem;
    border-bottom-width: var(--border-width-thin);
    border-bottom-style: solid;
    border-bottom-style: var(--border-style-default);
    border-bottom-color: #dddddd;
    border-bottom-color: var(--color-alto);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

h3:not(:last-child) {
    margin-bottom: 1.25rem;
    margin-bottom: var(--measure-medium);
}

.content > h3,
.widget h3 {
    clear: both;
    zoom: 1;
    width: auto;
    max-width: 100%;
    display: inline-block;
    padding-right: 2.25rem;
    padding-right: var(--measure-large);
}

.content > h3::before, .content > h3::after,
.widget h3::before,
.widget h3::after {
    content: "";
    display: table;
}

.content > h3::after,
.widget h3::after {
    clear: both;
}

@media screen and (max-width: 800px) {
    .content > h3,
    .widget h3 {
        align-self: flex-start;
    }
}

.lede + h3 {
    margin-top: 0;
}

/**
 * Heading - Delta
 */
.delta,
h4 {
    font-size: 1rem;
    font-size: var(--font-size-default);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

h4 {
    margin-bottom: 0.625rem;
    margin-bottom: var(--measure-small);
}

/**
 * Heading - Epsilon
 */
.epsilon,
h5 {
    font-size: 1rem;
    font-size: var(--font-size-default);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

/**
 * Heading - Zeta
 */
.zeta,
h6 {
    font-size: 1rem;
    font-size: var(--font-size-default);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

p {
    margin-top: 0;
    margin-bottom: 1.25rem;
    margin-bottom: var(--measure-medium);
}

p:last-child, p.ciu_embed {
    margin-bottom: 0;
}

p:empty {
    display: none;
    visibility: hidden;
}

@media screen and (min-width: 501px) and (max-width: 800px) {
    p.columns {
        column-count: 2;
        column-gap: 1.25rem;
    }
}

@media screen and (min-width: 1141px) {
    p.columns {
        column-count: 2;
        column-gap: 1.25rem;
    }
}

/**
 * Lists
 */
ol,
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

ol:not([class]),
ul:not([class]),
dl:not([class]),
.default-list {
    margin-bottom: 1.25rem;
    margin-bottom: var(--measure-medium);
}

ol:not([class]) ol:not([class]),
ol:not([class]) + ol:not([class]),
ul:not([class]) ol:not([class]),
ul:not([class]) + ol:not([class]),
dl:not([class]) ol:not([class]),
dl:not([class]) + ol:not([class]),
.default-list ol:not([class]),
.default-list + ol:not([class]), ol:not([class])
ul:not([class]),
ol:not([class]) +
ul:not([class]),
ul:not([class])
ul:not([class]),
ul:not([class]) +
ul:not([class]),
dl:not([class])
ul:not([class]),
dl:not([class]) +
ul:not([class]),
.default-list
ul:not([class]),
.default-list +
ul:not([class]), ol:not([class])
dl:not([class]),
ol:not([class]) +
dl:not([class]),
ul:not([class])
dl:not([class]),
ul:not([class]) +
dl:not([class]),
dl:not([class])
dl:not([class]),
dl:not([class]) +
dl:not([class]),
.default-list
dl:not([class]),
.default-list +
dl:not([class]), ol:not([class])
.default-list,
ol:not([class]) +
.default-list,
ul:not([class])
.default-list,
ul:not([class]) +
.default-list,
dl:not([class])
.default-list,
dl:not([class]) +
.default-list,
.default-list
.default-list,
.default-list +
.default-list {
    margin-top: 1.25rem;
    margin-top: var(--measure-medium);
}

ol:not([class]) li,
ul:not([class]) li,
.default-list li {
    padding-left: 2.25rem;
    padding-left: var(--measure-large);
    position: relative;
}

ol:not([class]) li:not(:last-child),
ul:not([class]) li:not(:last-child),
.default-list li:not(:last-child) {
    margin-bottom: 0.625rem;
    margin-bottom: var(--measure-small);
}

ol:not([class]) li::before,
ul:not([class]) li::before,
.default-list li::before {
    position: absolute;
    top: 0;
    left: 0;
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

ol:not([class]),
ol.default-list {
    counter-reset: ordered-list;
}

ol:not([class]) > li,
ol.default-list > li {
    counter-increment: ordered-list;
}

ol:not([class]) > li::before,
ol.default-list > li::before {
    content: counter(ordered-list, decimal) ".";
    font-variant-numeric: tabular-nums;
    font-feature-settings: "tnum";
}

ol:not([class])[reversed],
ol.default-list[reversed] {
    display: flex;
    flex-direction: column-reverse;
}

ul:not([class]) li::before,
ul.default-list li::before {
    content: "•";
    font-size: 1.5rem;
    line-height: 1;
}

dl {
    overflow: hidden;
}

dt {
    width: auto;
    min-width: 10rem;
    clear: left;
    padding-right: 1.25rem;
    padding-right: var(--measure-medium);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

@media screen and (min-width: 501px) {
    dt {
        float: left;
    }
}

dd {
    width: 1140px;
    width: var(--wrapper-width-large);
    display: table-cell;
    margin: 0;
    overflow: hidden;
    vertical-align: top;
}

dd:not(:last-child) {
    padding-bottom: 1.25rem;
    padding-bottom: var(--measure-medium);
}

@media screen and (max-width: 500px) {
    dd {
        width: auto;
        display: block;
        padding-left: 1.25rem;
        padding-left: var(--measure-medium);
    }
}

/**
 * Images and Figures
 */
img,
svg,
picture {
    margin: 0 auto;
    position: relative;
}

img,
svg,
picture,
a img,
a
svg,
a
picture {
    width: auto;
    max-width: 100%;
    height: auto;
}

picture,
picture img,
picture svg {
    display: block;
}

img {
    border-style: none;
}

svg:not(:root) {
    overflow: hidden;
}

figure {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0 1.25rem;
    margin: 0 0 var(--measure-medium);
}

figure:not(.highlight) {
    overflow-x: auto;
    overflow-y: hidden;
}

figure img, figure
svg, figure
picture,
figure > a,
figure > a img,
figure > a
svg,
figure > a
picture {
    background-clip: padding-box;
    border-radius: 0.375rem;
}

figure > a {
    display: block;
    text-decoration: none;
}

figure > a figcaption {
    color: #737373;
    color: var(--color-dove);
    font-style: italic;
}

figure figcaption {
    font-family: Georgia, serif;
    font-family: var(--font-family-serif);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1.25rem;
    padding-top: var(--measure-medium);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

/**
 * Tables
 */
table {
    min-width: 100%;
    flex: 1 0 auto;
    align-self: flex-start;
    border-width: 0.125rem;
    border-width: var(--border-width-thin);
    border-style: solid;
    border-style: var(--border-style-default);
    border-color: #dddddd;
    border-color: var(--color-alto);
    border-collapse: collapse;
    overflow-x: auto;
    overflow-y: hidden;
}

table.zebra tbody tr:nth-child(even) td {
    background-color: rgba(6, 6, 6, 0.05);
    background-color: var(--color-black--delta);
    border-left-color: #dddddd;
    border-left-color: var(--color-alto);
}

th,
td {
    transition: background-color 0.2s ease 0s, border-left-color 0.2s ease 0s;
    will-change: background-color, border-left-color;
    padding: 0.625rem;
    padding: var(--measure-small);
    text-align: left;
    user-select: text;
}

/*table*/
.highlight tbody tr:hover td,
.highlight--row tbody tr:hover td {
    border-left-color: #dddddd;
    border-left-color: var(--color-alto);
}

.highlight tbody tr:hover td,
.highlight tbody tr:hover + tr td,
.highlight--row tbody tr:hover td,
.highlight--row tbody tr:hover + tr td {
    border-top-color: #dddddd;
    border-top-color: var(--color-alto);
}

.highlight tbody tr:hover th,
.highlight tbody tr:hover td:not(:hover),
.highlight--row tbody tr:hover th,
.highlight--row tbody tr:hover td:not(:hover) {
    background-color: rgba(95, 138, 166, 0.1) !important;
    background-color: var(--color-raven--gamma) !important;
}

/*table*/
.highlight thead th:not(:empty),
.highlight td,
.highlight tfoot th:not(:empty),
.highlight--columns thead th:not(:empty),
.highlight--columns td,
.highlight--columns tfoot th:not(:empty) {
    position: relative;
}

.highlight thead th:not(:empty)::after,
.highlight td::after,
.highlight tfoot th:not(:empty)::after,
.highlight--columns thead th:not(:empty)::after,
.highlight--columns td::after,
.highlight--columns tfoot th:not(:empty)::after {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    transition: opacity 0.2s ease 0s;
    content: "";
    opacity: 0;
    background-color: rgba(95, 138, 166, 0.1);
    background-color: var(--color-raven--gamma);
    width: 100%;
    height: 200vw;
    pointer-events: none;
    z-index: 2;
    z-index: var(--z-index-above);
}

.highlight thead th:not(:empty):hover::after,
.highlight td:hover::after,
.highlight tfoot th:not(:empty):hover::after,
.highlight--columns thead th:not(:empty):hover::after,
.highlight--columns td:hover::after,
.highlight--columns tfoot th:not(:empty):hover::after {
    opacity: 1;
}

th {
    /*tfoot*/
}

th:not(:empty) {
    background-color: rgba(6, 6, 6, 0.05);
    background-color: var(--color-black--delta);
}

thead th,
tfoot th {
    font-weight: 600;
    font-weight: var(--font-weight-bold);
    white-space: nowrap;
}

tfoot th {
    border-top-width: 0.125rem;
    border-top-width: var(--border-width-thin);
    border-top-style: solid;
    border-top-style: var(--border-style-default);
    border-top-color: #dddddd;
    border-top-color: var(--color-alto);
}

@media screen and (min-width: 801px) {
    .totals th {
        text-align: right;
    }
}

.totals th::before {
    content: "Total:";
    padding-right: 0.5em;
    font-style: italic;
}

@media screen and (min-width: 501px) {
    .totals th::before {
        float: left;
    }
}

tbody th {
    font-style: italic;
    font-weight: 400;
    font-weight: var(--font-weight-default);
}

th + th {
    border-left-width: 0.125rem;
    border-left-width: var(--border-width-thin);
    border-left-style: solid;
    border-left-style: var(--border-style-default);
    border-left-color: #dddddd;
    border-left-color: var(--color-alto);
}

tbody th,
td {
    border-top-width: 0.125rem;
    border-top-width: var(--border-width-thin);
    border-top-style: solid;
    border-top-style: var(--border-style-default);
    border-top-color: #dddddd;
    border-top-color: var(--color-alto);
}

tbody th + td,
td + td {
    border-left-width: 0.125rem;
    border-left-width: var(--border-width-thin);
    border-left-style: solid;
