}

a:active {
    background-color: #ff0000;
    background-color: var(--color-canada);
    color: #f9f9f9;
    color: var(--color-white);
}

@supports not (text-decoration-skip: ink) {
    a {
        background-image: linear-gradient(to bottom, currentColor 75%, currentColor 75%);
        background-repeat: repeat-x;
        background-size: 1px 1px;
        background-position: 0 90%;
        text-decoration: none;
        text-shadow: -1px -1px 0 #f9f9f9, 1px -1px 0 #f9f9f9, -1px 1px 0 #f9f9f9, 1px 1px 0 #f9f9f9;
        text-shadow: -1px -1px 0 var(--color-white), 1px -1px 0 var(--color-white), -1px 1px 0 var(--color-white), 1px 1px 0 var(--color-white);
    }
}

a[rel*="external"]::after {
