 * Fragment Anchors inside Headings
 */
.fragment-anchor {
    text-transform: lowercase;
    font-family: "Input Mono", "Menlo for Powerline", "Menlo", Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
    font-family: var(--font-family-monospace);
