    height: 2em;
    padding-bottom: 0;
    margin-bottom: 0.182em;
    margin-bottom: var(--border-width-button);
    box-shadow: inset -1px 0 0 #507791;
    box-shadow: inset 0-var(--border-width-hairline) 0 0 var(--color-bowhead);
    transform: translateY(0.182em);
    transform: translateY(var(--border-width-button));
}

button::before, button::after,
.button::before,
.button::after {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    content: "";
    height: 0.182em;
    height: var(--border-width-button);
}

button:active::before, button:active::after,
.button:active::before,
.button:active::after {
    top: -0.182em;
    top: -var(--border-width-button);
}

button[rel*="external"]::after,
.button[rel*="external"]::after {
    position: relative;
    top: auto;
    right: auto;
    bottom: auto;
    left: auto;
    font-family: "Input Mono", "Menlo for Powerline", "Menlo", Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
    font-family: var(--font-family-monospace);
    font-variant-ligatures: none;
    will-change: initial;
    content: " →";
    width: auto;
    height: auto;
}

button[disabled],
.button[disabled] {
    opacity: 0.7;
    opacity: var(--opacity-beta);
    pointer-events: none;
}

/**
 * Apply moz fixes to `button` element only
 */
button {
    -webkit-font-smoothing: inherit;
}

button::-moz-focus-inner {
    padding: 0;
    margin: 0;
}

button::-moz-focusring {
    outline: 1px dotted ButtonText;
    outline: var(--border-width-hairline) var(--border-style-alternate) ButtonText;
}

.button--small {
    min-width: 5em;
    font-size: 1.25rem;
    font-size: var(--font-size-large);
    height: 2em;
    line-height: 1.818;
}

.button--small:active {
    height: 1.818em;
}

input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="range"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="week"],
input[type="url"],
optgroup,
select,
textarea {
    margin: 0;
    font-size: 1rem;
    font-size: var(--font-size-default);
}

input,
textarea {
    background-clip: padding-box;
    border-radius: 0.25rem;
    transition: border-color 0.2s ease 0s;
    will-change: border-color;
    box-sizing: inherit !important;
    padding: 0.4em;
    border-width: 0.125rem;
    border-style: solid;
    border-color: #dddddd;
    border-color: var(--color-alto);
    outline-style: none;
}

input:hover,
textarea:hover {
    border-color: #759ab3;
    border-color: var(--color-coyote);
}

input:focus,
textarea:focus {
    border-color: #5f8aa6;
    border-color: var(--color-raven);
}

input[disabled],
textarea[disabled] {
    opacity: 0.7;
    opacity: var(--opacity-beta);
    pointer-events: none;
}

input::-moz-focus-inner,
textarea::-moz-focus-inner {
    padding: 0;
    margin: 0;
}

input {
    overflow: visible;
}

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

input[type="search"] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

input::-webkit-file-upload-button {
    -webkit-appearance: button;
}

textarea {
    overflow: auto;
}

/**
 * Fix `audio`, `iframe`, and `video`
 */
audio,
iframe,
video {
    width: 100%;
    border: 0;
    overflow-x: hidden;
}

iframe {
    margin-bottom: 1.25rem;
    margin-bottom: var(--measure-medium);
    contain: strict;
}

.wrap {
    width: auto;
    max-width: 1140px;
    max-width: var(--wrapper-width-large);
    padding-left: 1.25rem;
    padding-left: var(--gutter-width);
    padding-right: 1.25rem;
    padding-right: var(--gutter-width);
    margin-left: auto;
    margin-right: auto;
}

.wrap--full-width {
    max-width: 100%;
}

.clear {
    clear: both;
    zoom: 1;
}

.clear::before, .clear::after {
    content: "";
    display: table;
}

.clear::after {
    clear: both;
}

main {
    display: block;
    padding-bottom: 2.25rem;
    padding-bottom: var(--measure-large);
}

header {
    display: flex;
    overflow: hidden;
    position: relative;
}

@supports (display: grid) {
    header {
        display: grid;
    }
}

@media screen and (max-width: 800px) {
    header {
        flex-direction: column;
        justify-content: center;
    }
    @supports (display: grid) {
        header {
            grid-template-rows: 7rem min-content;
        }
    }
}

@media screen and (min-width: 801px) {
    header {
        justify-content: space-between;
    }
    @supports (display: grid) {
        header {
            grid-template-columns: 15.875rem min-content;
        }
    }
}

header::before {
    content: "";
    background-color: #5f8aa6;
    background-color: var(--color-raven);
    height: 0.25rem;
    height: var(--border-width-default);
    position: absolute;
    bottom: 0;
    left: 1.25rem;
    left: var(--gutter-width);
    right: 1.25rem;
    right: var(--gutter-width);
}

/**
 * The width of the content is fluid, and always has padding on the left-hand
 * side to make room for any `aside`, which can appear anywhere inside
 * `.content`.
 */
.content {
    padding-top: 2.25rem;
    padding-top: var(--measure-large);
}

@media screen and (max-width: 800px) {
    .content {
        display: flex;
        flex-direction: column;
    }
}

@media screen and (min-width: 801px) {
    .content {
        padding-left: 18.125rem;
    }
}

/**
 * The width of the asides is static above the medium breakpoint and always
 * floated within an article or list of articles. The math is calculated as in
 * `layout/_content.scss`.
 */
aside {
    font-family: Georgia, serif;
    font-family: var(--font-family-serif);
}

@media screen and (max-width: 800px) {
    aside {
        flex: 1 0 100%;
    }
}

@media screen and (min-width: 801px) {
    aside {
        width: 15.875rem;
        width: var(--aside-width);
        float: left;
        margin-left: -18.125rem;
        margin-bottom: 2.25rem;
        margin-bottom: var(--measure-large);
    }
}

@media screen and (max-width: 800px) {
    aside:not(.author):not(:last-child) {
        margin-bottom: 1.25rem;
        margin-bottom: var(--measure-medium);
    }
}

h3 + aside {
    margin-top: 2.25rem;
    margin-top: var(--measure-large);
}

aside figure {
    margin-bottom: 0;
}

aside figcaption,
aside p {
    clear: both;
}

@media screen and (min-width: 501px) {
    aside figcaption,
    aside p {
        font-size: 0.875rem;
        font-size: var(--font-size-small);
    }
}

footer {
    width: 100%;
    padding: 2.25rem 0;
    padding: var(--measure-large) 0;
    border-top-width: 0.25rem;
    border-top-width: var(--border-width-default);
    border-top-style: solid;
    border-top-style: var(--border-style-default);
    border-top-color: #5f8aa6;
    border-top-color: var(--color-raven);
}

@keyframes anchor-target {
    0%,
    50% {
        background-color: var(--color-highlight);
    }
    100% {
        background-color: transparent;
        background-color: var(--color-transparent);
    }
}

/**
 * Anchor Target Animation
 */
h1[id]:target,
h2[id]:target,
h3[id]:target,
h4[id]:target,
h5[id]:target,
h6[id]:target,
.endnote[id]:target,
.endnote-anchor[id]:target {
    animation: anchor-target 2s linear;
    animation: anchor-target var(--animation-duration) var(--animation-function);
}

.border-radius,
.round,
.rounded {
    background-clip: padding-box;
    border-radius: 0.375rem;
}

.border-radius--thin,
.round--thin,
.rounded--thin {
    background-clip: padding-box;
    border-radius: 0.25rem;
}

[aria-hidden="true"]:not([href]), [aria-hidden="true"][href]:not(:focus):not(:active),
.hidden:not([href]),
.hidden[href]:not(:focus):not(:active),
.hide:not([href]),
.hide[href]:not(:focus):not(:active),
.sr-only:not([href]),
.sr-only[href]:not(:focus):not(:active),
.visually-hidden:not([href]),
.visually-hidden[href]:not(:focus):not(:active) {
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    margin: -1px !important;
    border: 0 !important;
    overflow: hidden !important;
    position: absolute !important;
    white-space: nowrap !important;
    clip: rect(0 0 0 0) !important;
    clip-path: inset(100%) !important;
}

.hyphen,
.hyphenate {
    overflow-wrap: break-word;
    hyphens: auto;
}

.non-selectable {
    user-select: none;
    -webkit-touch-callout: default;
    -webkit-touch-callout: none;
}

.logo {
    user-select: none;
    -webkit-touch-callout: default;
    -webkit-touch-callout: none;
    height: 6.25rem;
    flex: 1 0 15.875rem;
    flex: 1 0 var(--aside-width);
    display: inline-block;
    position: relative;
    text-decoration: none;
}

.logo svg {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: transform 1s ease 0s;
    transition: transform var(--transition-duration-long) ease 0s;
    will-change: transform;
    fill: #5f8aa6;
    fill: var(--color-raven);
    width: 8.5rem;
    width: var(--logo-size);
    height: 8.5rem;
    height: var(--logo-size);
    transform: translateX(-65%) translateY(-40%) rotateZ(5deg);
    transform: translateX(-65%) translateY(-40%) rotateZ(var(--rotation-default));
}

@media screen and (prefers-reduced-motion: reduce) {
    .logo svg {
        transition: none 0.2s ease 0s;
    }
}

.logo:hover svg, .logo:focus svg {
    transform: translateX(-40%) translateY(-30%);
}

.logo:active {
    background-color: transparent;
    background-color: var(--color-transparent);
}

.logo:active svg {
    transition-duration: 0.2s;
    transition-duration: var(--transition-duration-default);
    transform: translateX(-30%) translateY(-25%);
}

.nav-primary {
    padding-top: 1.25rem;
    padding-top: var(--measure-medium);
    padding-bottom: 1.25rem;
    padding-bottom: var(--measure-medium);
    white-space: nowrap;
}

@media screen and (max-width: 800px) {
    .nav-primary {
        flex: 1 0 100%;
        padding-top: 0.625rem;
        padding-top: var(--measure-small);
        text-align: center;
    }
}

.nav-primary-list {
    font-size: 0;
    height: 3.75rem;
}

.nav-primary-list li {
    font-size: 1rem;
    font-size: var(--font-size-default);
}

.nav-primary-list li {
    display: inline-block;
}

@media screen and (max-width: 500px) {
    .nav-primary-list li + li {
        margin-left: 0.25em;
    }
}

@media screen and (min-width: 501px) and (max-width: 800px) {
    .nav-primary-list li + li {
        margin-left: 0.5em;
    }
}

@media screen and (min-width: 801px) {
    .nav-primary-list li + li {
        margin-left: 1em;
        margin-left: var(--measure-one-relative);
    }
}

.nav-primary-list a {
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: "League Gothic", Impact, sans-serif;
    font-family: var(--font-family-alpha);
    transition: color 0.2s ease 0s, padding-bottom 0.2s ease 0s, border-bottom-width 0.2s ease 0s, transform 0.2s ease 0s;
    will-change: color, padding-bottom, border-bottom-width, transform;
    color: #737373;
    color: var(--color-dove);
    display: block;
    padding: 0.75rem 1rem;
    border-width: 0;
    border-style: solid;
    border-style: var(--border-style-default);
    border-color: #5f8aa6;
    border-color: var(--color-raven);
    margin-bottom: 0.25rem;
    margin-bottom: var(--border-width-default);
    font-size: 1.25rem;
    font-size: var(--font-size-large);
    line-height: 1.25;
    line-height: var(--line-height-small);
    text-decoration: none;
}

.nav-primary-list a:active {
    background-color: transparent;
    background-color: var(--color-transparent);
    transform: translateY(0.2rem);
}

@media screen and (max-width: 500px) {
    .nav-primary-list a {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
    }
}

.nav-primary-list a:hover,
.nav-primary-list a:focus,
.nav-primary-list a:active,
[class*="page--home"] [class*="nav-primary--home"] a,
[class*="page--about"] [class*="nav-primary--about"] a,
[class*="page--article"] [class*="nav-primary--article"] a,
[class*="page--pen"] [class*="nav-primary--pen"] a,
[class*="page--search"] [class*="nav-primary--search"] a {
    color: #4f4f4f;
    color: var(--color-kaiser);
    padding-bottom: 0.5rem;
    border-bottom-width: 0.25rem;
    border-bottom-width: var(--border-width-default);
}

.title,
.tagline {
    width: 100%;
    margin-bottom: 2.25rem;
    margin-bottom: var(--measure-large);
}

/**
 * Banner / Hero Image
 * Can take two images, which are displayed with `srcset`
 */
.banner {
    background-clip: padding-box;
    border-radius: 0.375rem;
    width: 50.625rem;
    width: var(--article-width);
    margin-bottom: 2.25rem;
    margin-bottom: var(--measure-large);
}

@media screen and (max-width: 500px) {
    .banner {
        width: 500px;
        width: var(--wrapper-width-small);
    }
}

/**
 * Post Meta
 */
.meta {
    padding-bottom: 1.25rem;
    padding-bottom: var(--measure-medium);
    overflow: hidden;
    font-style: italic;
}

aside + .meta {
    margin-top: -0.625rem;
    margin-top: -var(--measure-small);
}

.meta time,
.meta div {
    display: inline-block;
    margin-right: 2.25rem;
    margin-right: var(--measure-large);
}

.meta time + div,
.meta div + div {
    position: relative;
}

.meta time + div::before,
.meta div + div::before {
    content: "–";
    color: #737373;
    color: var(--color-dove);
    position: absolute;
    left: -1.25rem;
    left: -var(--measure-medium);
    transform: translateX(-50%);
}

.meta a,
.meta strong {
    color: inherit;
    font-weight: 600;
    font-weight: var(--font-weight-bold);
    font-style: normal;
}

.meta a {
    text-decoration-color: transparent;
    text-decoration-color: var(--color-transparent);
}

.meta a:hover, .meta a:focus, .meta a:active {
    text-decoration-color: #ff0000;
    text-decoration-color: var(--color-canada);
}

.meta a:active {
    color: #f9f9f9;
    color: var(--color-white);
}

/**
 * Page Introduction / Description
 */
.lede {
    margin-bottom: 2.25rem;
    margin-bottom: var(--measure-large);
}

@media screen and (max-width: 800px) {
    .lede {
        text-align: left;
    }
}

aside + .lede {
    margin-top: -0.625rem;
    margin-top: -var(--measure-small);
}

.meta + .lede {
    border-top-width: 0.125rem;
    border-top-width: var(--border-width-thin);
    border-top-style: solid;
    border-top-style: var(--border-style-default);
    border-top-color: #dddddd;
    border-top-color: var(--color-alto);
    padding-top: 1.25rem;
    padding-top: var(--measure-medium);
}

.lede--featured {
    max-width: 24em;
    font-size: 1.25rem;
    font-size: var(--font-size-large);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

/**
 * Explain required terminology for the content
 */
/*dl*/
.terminology {
    padding-bottom: 2.25rem;
    padding-bottom: var(--measure-large);
    border-bottom-width: 0.125rem;
    border-bottom-width: var(--border-width-thin);
    border-bottom-style: solid;
    border-bottom-style: var(--border-style-default);
    border-bottom-color: rgba(6, 6, 6, 0.1);
    border-bottom-color: var(--color-black--gamma);
}

/**
 * Page Edit Notes
 */
.edit {
    background-clip: padding-box;
    border-radius: 0.375rem;
    background-color: rgba(95, 138, 166, 0.1);
    background-color: var(--color-raven--gamma);
    width: 100%;
    max-width: calc(336px + (480 - 336) * ((100vw - 500px) / (1140 - 500)));
    max-width: var(--line-length-responsive);
    padding: 1.25rem;
    padding: var(--measure-medium);
    margin: 0 auto 2.25rem;
    margin: 0 auto var(--measure-large);
    line-height: 1.429;
    line-height: var(--line-height-medium);
}

.edit time {
    margin-bottom: 0.625rem;
    margin-bottom: var(--measure-small);
    font-size: 1.125rem;
    font-size: var(--font-size-gamma);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

.content-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 2.25rem;
    margin-bottom: var(--measure-large);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

.page--home .content-list,
.widget .content-list {
    margin-bottom: 0;
}

@media screen and (max-width: 500px) {
    .content-list li {
        width: 100%;
        display: inline-block;
    }
    .content-list li + li {
        margin-top: 1.25rem;
        margin-top: var(--measure-medium);
    }
}

@media screen and (min-width: 501px) {
    .content-list li {
        width: 49.01235%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
}

@media screen and (min-width: 501px) {
    .content-list li:nth-child(n+3) {
        margin-top: 1.25rem;
        margin-top: var(--measure-medium);
    }
}

aside .content-list li {
    width: 100%;
}

aside .content-list li + li {
    margin-top: 0.625rem;
    margin-top: var(--measure-small);
}

.content-list article {
    flex: 1 0 100%;
    display: flex;
    flex-direction: column;
}

.content-list a {
    background-clip: padding-box;
    border-radius: 0.375rem;
    flex: 1 0 100%;
    display: block;
    padding: 1.25rem;
    padding: var(--measure-medium);
    overflow: hidden;
    text-decoration: none;
}

.content-list a:hover, .content-list a:focus, .content-list a:active {
    background-color: rgba(95, 138, 166, 0.1) !important;
    background-color: var(--color-raven--gamma) !important;
}

aside .content-list a {
    padding: 0.625rem;
    padding: var(--measure-small);
}

.content-list .title,
.content-list .lede,
.content-list .date {
    text-shadow: 0 1px 0 #f9f9f9;
    text-shadow: 0 var(--border-width-hairline) 0 var(--color-white);
}

.content-list .title {
    transition: text-decoration-color 0.2s ease 0s;
    will-change: text-decoration-color;
    width: auto;
    display: inline-block;
    margin-bottom: 0;
    text-decoration: underline;
    text-decoration-color: #dddddd;
    text-decoration-color: var(--color-alto);
    text-underline-position: under;
}

.content-list a:hover .title,
.content-list a:focus .title,
.content-list a:active .title {
    color: #5f8aa6;
    color: var(--color-raven);
    text-decoration-color: #ff0000;
    text-decoration-color: var(--color-canada);
}

.content-list .lede,
.content-list .date {
    color: #737373;
    color: var(--color-dove);
    margin-top: 0.625rem;
    margin-top: var(--measure-small);
    font-size: 0.875rem;
    font-size: var(--font-size-small);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

.content-list .lede {
    margin-bottom: 0;
}

@media screen and (min-width: 801px) {
    .content-list .date {
        text-align: right;
    }
}

.content-list .date::before {
    content: "– ";
}

.content-list svg {
    transition: opacity 0.2s ease 0s, transform 0.2s ease 0s;
    will-change: opacity, transform;
    opacity: 0.05;
    opacity: var(--opacity-delta);
    stroke: #4f4f4f;
    stroke: var(--color-kaiser);
    width: 6.25em !important;
    height: 6.25em !important;
    position: absolute;
    top: 50%;
    right: 0.625rem;
    right: var(--measure-small);
    transform: translateY(-50%);
    z-index: 0;
    z-index: var(--z-index-root);
