        height: 4em !important;
    }
}

.content-list a:hover svg,
.content-list a:focus svg,
.content-list a:active svg {
    opacity: 0.1;
    opacity: var(--opacity-gamma);
    transform: translateX(-2.25rem) translateY(-50%) scale(1.2);
    transform: translateX(-var(--measure-large)) translateY(-50%) scale(1.2);
