.search-main {
    max-width: 500px;
    max-width: var(--wrapper-width-small);
    margin-bottom: 2.25rem;
    margin-bottom: var(--measure-large);
}

.search-results {
    margin-top: 1.25rem;
    margin-top: var(--measure-medium);
}

.search-results .content-list {
    margin-bottom: 0;
}

.search-form {
    position: relative;
    display: flex;
}

.search-input {
    background-color: #f9f9f9;
    background-color: var(--color-white);
