.code-toggle-label {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: pointer;
}

.code-toggle-input:checked ~ .code-toggle-label {
    display: none;
    visibility: hidden;
}

.code-toggle-label button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    pointer-events: none;
}

