////
/// Media
/// @group base
/// @link https://chrisburnell.com/styleguide/#figure
////


audio,
embed,
iframe,
object,
video {
    @extend %media-elements !optional;
}

/**
 * Fix `audio`, `iframe`, and `video`
 */
audio,
iframe,
video {
    width: 100%;
    border: 0;
    overflow-x: hidden;
}
iframe {
    @include v(margin-bottom, medium);
    contain: strict;
}
